








import { Component, Vue, Inject,Prop } from 'vue-property-decorator';
import {Cert, CertOwn} from '@/entry/diamond';
import 'vant/lib/cell/style';

@Component
export default class CertShow extends Vue {
    // @Inject() // todo not work
    @Inject()
    private certOwn!: CertOwn;
    // 处理证书中的多语言
    public get certMap(): Array<{key: string, value: string}> {
        if (!this.certOwn) {
            this.certOwn = this.$store.state.data.certOwn;
        }
        const mapI18n = [];
        if (this.certOwn && this.certOwn.cert) {
            mapI18n.push(...this.certOwn.cert.map);
            if (this.certOwn.cert.i18n) {
                for (const it of this.certOwn.cert.map.keys()) {
                    const item = this.certOwn.cert.map[it];
                    const l = this.$t(item.key) as string;
                    if (l) {
                        item.key = l;
                        mapI18n[it] = item;
                    }
                }
            }
        }
        return mapI18n;
    }
}
