






















import { Component, Vue } from 'vue-property-decorator';
import {Button} from 'vant';
import CertShow from '@/componets/cert_show.vue';
import {CertOwn} from '@/entry/diamond';
import ScanSign from '@/componets/scan_sign.vue';

import 'vant/lib/button/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';

Vue.use(Button);

const arrowDown = 'arrow-down';
const arrowUp = 'arrow-up';

@Component({
    components: {CertShow, ScanSign},
})
export default class Prepare extends Vue {
    private certOwn!: CertOwn;
    private arrowName = arrowDown;
    private code = '';
    private showQr = false;
    public created(): void {
      this.certOwn = this.$store.state.data.certOwn;
      this.$store.state.title = this.$t('cert.prepare');
      // todo make the code
      this.code = 'test';
    }

    public onClickArrow(): void {
      if (this.arrowName === arrowDown) {
          this.arrowName = arrowUp;
          this.showQr = true;
      } else {
          this.arrowName = arrowDown;
          this.showQr = false;
      }
    }

    public onClickConfirm(): void {
        this.arrowName = arrowDown;
        this.showQr = false;
        this.$router.push('/frame/cert/confirm');
    }
}
